import { _decorator, Component, v3, tween, Prefab, Input, director, find, Animation, game, CCBoolean, CCInteger, Node, UITransform, Tween, Label, ProgressBar } from 'cc';


import { Resoure_Data, Scene_Change_Type, } from '../Tool/Resoure_Data';


const { ccclass, property } = _decorator;

@ccclass('Script_Scene_Change')
export default class Script_Scene_Change extends Component {

    Resoure_Data: Resoure_Data = null;

    @property({})
    Path: string = ""

    /**@param 0:退出游戏 1:自动跳转 2:手动跳转*/
    /**场景改变类型 */
    @property({ tooltip: "0:自动跳转 1:手动跳转 2:结束游戏" })
    Scene_Change_Type: Scene_Change_Type = Scene_Change_Type.Automatic;

    @property({ type: Node })
    Progress_Mask: Node = null;

    @property({ type: Node })
    Progress_Bar: Node = null;

    @property({ type: Label })
    Value: Label = null;

    async onLoad() {
        this.Resoure_Data = find("Resoure_Data").getComponent(Resoure_Data);
        if (this.Scene_Change_Type == Scene_Change_Type.Automatic) {
            this.Scene_Jump_Automatic();
        }
    }

    start() {
        // Input.EventType.TOUCH_START= "touch-start";
        this.node.on("touch-end", this.Scene_Jump_Manual, this);
    }

    /**自动跳转 */
    async Scene_Jump_Automatic() {
        this.Resoure_Data.Scene_Name_Next = this.Path;
        console.log("LoadScene:Game");
        let SceneGame = await this.Resoure_Data.Load_Scene(this.Resoure_Data.Scene_Name_Next, this.Progress_Mask, this.Progress_Bar, this.Value, "Vertical");
        this.Resoure_Data.Director_Run(SceneGame);

    }

    /**手动跳转 */
    Scene_Jump_Manual() {


        if (this.Scene_Change_Type == Scene_Change_Type.Finish) {//退出游戏



        } else if (this.Scene_Change_Type == Scene_Change_Type.Automatic) { return; }//加载页


    }


}

